<template>
    <form @submit.prevent="authUser">
       
        <b-form-input
          id="input-1"
          v-model.trim="login"
          type="text"
          required
          placeholder=" "
        ></b-form-input>
        <p v-if="$v.login.$dirty && !$v.login.required"> Login    </p>
        <p v-if="$v.login.$dirty && !$v.login.minLength"> Login     6 .  {{login.length}}</p>
        <p v-if="$v.login.$dirty && !$v.login.maxLength"> Login     20 .  {{login.length}}</p>
        <b-form-input
          id="input-1"
          v-model.trim="email"
          type="email"
          required
          placeholder=" "
        ></b-form-input>
        <p v-if="$v.email.$dirty && !$v.email.required"> email    </p>
        <p v-if="$v.email.$dirty && !$v.email.minLength"> email     6 .  {{email.length}}</p>
        <p v-if="$v.email.$dirty && !$v.email.maxLength"> email     20 .  {{email.length}}</p>
        <p v-if="$v.email.$dirty && !$v.email.email">Email   </p>
        <b-form-input
          id="input-1"
          v-model="pass"
          type="password"
          required
          placeholder=" "
        ></b-form-input>
        <p v-if="$v.pass.$dirty && !$v.pass.required"> pass    </p>
        <p v-if="$v.pass.$dirty && !$v.pass.minLength"> pass     6 .  {{pass.length}}</p>
        <p v-if="$v.pass.$dirty && !$v.pass.maxLength"> pass     20 .  {{pass.length}}</p>
        <b-button type="submit" variant="primary"></b-button>
        {{getRegStatus}}
    </form>



</template>



<script>

import {mapActions, mapGetters} from "vuex";

import {required, minLength, maxLength, email} from "vuelidate/lib/validators"

export default {
    data(){
        return {
            login: "",
            pass: "",   
            email: "",
        }
    },
     validations: {
        email: {required, minLength: minLength(6), maxLength: maxLength(20), email},
        login: {required, minLength: minLength(6), maxLength: maxLength(20)},
        pass: {required, minLength: minLength(6) , maxLength: maxLength(20)}
    },
    methods: {
        ...mapActions([
            "userReg"
        ]),
        authUser() {
            if(this.$v.$invalid){
                this.$v.$touch()
                return
            }
            this.userReg({login: this.login, pass: this.pass, email: this.email});
            //this.$store.dispatch("checkStatus");
        }
    },
    computed: mapGetters([
        "getRegStatus"
    ]),
}
</script>

<style lang="scss" scoped>
    form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    input, button {
        margin-top: 20px;
        max-width: 500px;
        
    }
</style>