<template>
    <div>
        
        <p>  {{time | date("time")}}</p> 
         <b-tabs content-class="mt-3" v-if="!getStatus">
            <b-tab title="" active><Auth></Auth></b-tab>
            <b-tab title=""><Regist></Regist></b-tab>
        </b-tabs>
        <div v-else>
            <h1> {{getUserData.login}}</h1>
            <p>  {{getUserData.token}}</p>
            <button v-on:click="logOut">Logout</button>
            <button v-on:click="GetTodo">Get todo</button>
            <p>Current todo - {{getTodoList}}</p>
            <p>Add type todo <input type="text" v-model="todoType"><button v-on:click="createTodo">New type todo</button></p>
            <ul v-for="(item, index) in getTodoId" :key="item.id">
                <li>
                    <b-card bg-variant="dark" :header="'Todo #'+ (index+1)" text-variant="white" class="text-center">
                        <b-card-text><router-link class="todo-link" :to="{ name: 'todo', params: { id: item }}">{{getTodo[index]}}</router-link></b-card-text>
                    </b-card>
                    <b-button-close class="close__btn" text-variant="danger" v-on:click="removeTodo(item)"></b-button-close>
                </li>
            </ul>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import Auth from '@/components/Auth.vue'
import Regist from '@/components/Regist.vue'

import {mapGetters} from "vuex";

export default {
    name: "user",
    data: () => ({
        time: "",
        interval: null,
        newTodo: "",
        todoType: "",
        todoList: ["test", "one"],
    }),
    components: {
        Auth,
        Regist
    },
    methods: {
        logOut(){
            this.$store.commit('logOut')
        },
        GetTodo(){
            this.$store.dispatch("GetTodo");
        },

        removeTodo(id){
            console.log(id)
            let temp = JSON.parse(this.getTodoList)
            delete temp[id]

            this.$store.dispatch("CreateTodo", [temp,this.getUserData.id])

            this.todoList = Object.keys(JSON.parse(this.getTodoList));
            
        },

        createTodo(){
            let todoList = JSON.parse(this.getTodoList)
            todoList[Date.now()] = [this.todoType, []]
            this.$store.dispatch("CreateTodo", [todoList, this.getUserData.id])

            this.todoList = Object.keys(JSON.parse(this.getTodoList));
            this.todoType = ""
        }
    },
    mounted(){
      
        this.interval = setInterval(() => {
            this.time =  new Date();
        }, 1000);
       
        this.$store.dispatch("checkStatus");
        //this.$store.dispatch("GetTodo");
       
        
    },
    computed: mapGetters(['getStatus', 'getUserData', 'getTodoList', 'getTodo', 'getTodoId']),

    beforeDestroy(){
        clearInterval(this.interval);
    }
}
</script>

<style lang="scss" scoped>
    .nav-tabs {
        flex-direction: row;
    }
    .todo-link {
        color: white;
        transition: .5 ease;
        &:hover{
            color: darken($color: #fff, $amount: 50)
        }
    }
    li {
        width: 500px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 50px;
        div{
            width: 100%;
        }
    }
   
</style>

<style lang="scss">
    .nav-tabs {
        display: flex;
        flex-direction: row;
    }
</style>