<template>
    <form @submit.prevent="authUser">
         <b-alert v-if="getErrorMessage['status']" show variant="danger">{{getErrorMessage['message']}}</b-alert>
         <b-form-input
          id="input-1"
          v-model="login"
          type="text"
          required
          v-model.trim="login"
          placeholder=" "
        ></b-form-input>
        <p v-if="$v.login.$dirty && !$v.login.required"> Login    </p>
        <p v-if="$v.login.$dirty && !$v.login.minLength"> Login     6 .  {{login.length}}</p>
         <b-form-input
          id="input-1"
          v-model="pass"
          type="password"
          required
          v-model.trim="pass"
          placeholder=" "
        ></b-form-input>
        <p v-if="$v.pass.$dirty && !$v.pass.required"> Password    </p>
        <p v-if="$v.pass.$dirty && !$v.pass.minLength"> Password     6 .  {{pass.length}}</p>
        <b-button type="submit" variant="primary"></b-button>
        
    </form>
</template>


<script>

import {mapMutations, mapActions, mapGetters} from "vuex";

import {required, minLength} from "vuelidate/lib/validators"

export default {
    data(){
        return {
            login: "",
            pass: "",        
        }
    },
    validations: {
        login: {required, minLength: minLength(6)},
        pass: {required, minLength: minLength(6)}
    },
    computed: mapGetters([
        "getUserData",
        "getErrorMessage"
        ]),
    methods: {
        ...mapMutations([
            "changeStatus",
        ]),
        ...mapActions([
            "userLogin"
        ]),
        authUser() {
            //this.changeStatus();
            if(this.$v.$invalid){
                this.$v.$touch()
                return
            }
            this.userLogin({login: this.login, pass: this.pass});
        }
    }
}
</script>

<style lang="scss" scoped>
    form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    input, button {
        margin-top: 20px;
        max-width: 500px;
        
    }
    .invalid {
        color: red;
    }
</style>